// --- CategoryFilter and NavbarExtrasPortal (Keep these components exactly as they were) ---
const CategoryFilter = ({ categories, selectedCategory, setSelectedCategory, loadingCategories }) => {
    const [isDropdownOpen, setIsDropdownOpen] = useState(false);
    const allCategories = ['All', ...categories.map(cat => cat.name)];

    if (loadingCategories) {
        return <div className="h-10 w-48 bg-gray-200 animate-pulse rounded-md"></div>;
    }

    return (
        <div className="relative">
            <button
                type="button"
                className="w-48 px-4 py-2 text-left bg-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-sky-500 focus:border-transparent transition-colors duration-200 hover:bg-gray-50 text-sm font-medium text-gray-700 flex justify-between items-center"
                onClick={() => setIsDropdownOpen(!isDropdownOpen)}
            >
                {selectedCategory}
                <svg className={`h-5 w-5 transition-transform duration-200 ${isDropdownOpen ? 'rotate-180' : ''}`} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                    <path fillRule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clipRule="evenodd" />
                </svg>
            </button>
            {isDropdownOpen && (
                <div className="absolute z-20 mt-2 w-full origin-top-right bg-white rounded-md shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none transition-all duration-100 ease-in-out">
                    <div className="py-1">
                        {allCategories.map(cat => (
                            <div
                                key={cat}
                                className="block w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 cursor-pointer"
                                onClick={() => {
                                    setSelectedCategory(cat);
                                    setIsDropdownOpen(false);
                                }}
                            >
                                {cat}
                            </div>
                        ))}
                    </div>
                </div>
            )}
        </div>
    );
};

const NavbarExtrasPortal = ({ children }) => {
    const [mountNode, setMountNode] = useState(null);

    useEffect(() => {
        const node = document.getElementById('navbar-extras-portal-target');
        setMountNode(node);
        // Clean up function to clear content when component unmounts or children change
        return () => {
            if (node) {
                node.innerHTML = '';
            }
        };
    }, []); // Empty dependency array ensures this runs only once on mount

    // Re-render children if they change while the portal target is available
    useEffect(() => {
        if (mountNode) {
            // Clear previous content before rendering new children
            mountNode.innerHTML = '';
            // Render new children - React handles this via createPortal
        }
    }, [children, mountNode]);

    return mountNode ? createPortal(children, mountNode) : null;
};
// --- End of CategoryFilter and NavbarExtrasPortal ---


const BlogPage = () => {
    const [posts, setPosts] = useState([]);
    const [page, setPage] = useState(0);
    const [hasMore, setHasMore] = useState(true);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const [categories, setCategories] = useState([]);
    const [loadingCategories, setLoadingCategories] = useState(true);
    const [selectedCategory, setSelectedCategory] = useState("All");
    const [showMobileFilters, setShowMobileFilters] = useState(false);
    const [searchParams] = useSearchParams();
    const searchTerm = searchParams.get('q') || "";
    const [categoriesMap, setCategoriesMap] = useState({});
    const [isDataReady, setIsDataReady] = useState(false);

    // NEW: State for mobile tab navigation
    const [activeMobileTab, setActiveMobileTab] = useState('blog'); // 'news', 'blog', 'market'

    const observer = useRef();
    const lastPostElementRef = useCallback(node => {
        // NEW: Only attach observer if blog tab is active
        if (loading || activeMobileTab !== 'blog') return;
        if (observer.current) observer.current.disconnect();
        observer.current = new IntersectionObserver(entries => {
            if (entries[0].isIntersecting && hasMore) {
                setPage(prevPage => prevPage + 1);
            }
        });
        if (node) observer.current.observe(node);
    }, [loading, hasMore, activeMobileTab]); // Added activeMobileTab dependency

    // Reset posts when category or search term changes
    useEffect(() => {
        setPosts([]);
        setPage(0);
        setHasMore(true);
        // No need to reset activeMobileTab here unless intended
    }, [selectedCategory, searchTerm]);

    // Fetch posts when page, category, search term, or data readiness changes
    useEffect(() => {
        if (!isDataReady) return;

        setLoading(true);
        setError('');
        // NOTE: We fetch all posts regardless of the mobile tab for simplicity,
        // but filter them later. For optimization, you could pass the category
        // to the API if needed.
        getPaginatedPosts(page, 9).then(response => {
            setPosts(prevPosts => {
                const newPosts = response.data.content;
                const existingIds = new Set(prevPosts.map(p => p.id));
                const uniqueNewPosts = newPosts.filter(p => !existingIds.has(p.id));
                // If it's the first page load or filters changed, replace posts, otherwise append
                return page === 0 ? newPosts : [...prevPosts, ...uniqueNewPosts];
            });
            setHasMore(!response.data.last);
            setLoading(false);
        }).catch(err => {
            setError('Failed to fetch blog posts.');
            console.error("Fetch posts error:", err); // Log error
            setLoading(false);
        });
    }, [page, isDataReady]); // Removed selectedCategory, searchTerm dependencies here as filtering happens in useMemo


    // Fetch categories on initial mount
    useEffect(() => {
        setLoadingCategories(true);
        getCategories().then(response => {
            setCategories(response.data);
            const newCategoriesMap = response.data.reduce((acc, cat) => {
                acc[cat.name] = cat.slug;
                return acc;
            }, {});
            setCategoriesMap(newCategoriesMap);
        }).catch(err => console.error("Failed to fetch categories:", err))
            .finally(() => {
                setLoadingCategories(false);
                setIsDataReady(true); // Mark data as ready after categories are loaded
            });
    }, []);

    // Memoized filtering of posts based on selected category and search term
    const filteredPosts = useMemo(() => {
        let postsToFilter = posts;
        if (selectedCategory !== "All") {
            postsToFilter = postsToFilter.filter(p => p.category?.name === selectedCategory);
        }
        if (searchTerm) {
            const term = searchTerm.toLowerCase();
            postsToFilter = postsToFilter.filter(p =>
                p.title.toLowerCase().includes(term) ||
                (p.content && p.content.toLowerCase().includes(term)) // Optional: search content too
            );
        }
        // Ensure sorting happens correctly
        return [...postsToFilter].sort((a, b) => new Date(b.updatedAt || b.createdAt) - new Date(a.updatedAt || a.createdAt));
    }, [posts, selectedCategory, searchTerm]);

    // --- Keep existing useMemo hooks for mobileLayout and desktopLayoutBlocks ---
    const mobileLayout = useMemo(() => {
        return filteredPosts.map(post => {
            const isStory = post.thumbnails && post.thumbnails.length > 1;
            const layout = isStory ? 'banner' : 'grid';
            return { ...post, layout };
        });
    }, [filteredPosts]);

    const desktopLayoutBlocks = useMemo(() => {
        if (filteredPosts.length === 0) return [];
        const blocks = []; let currentDefaultBlock = []; const multiColumnGroups = {};
        filteredPosts.forEach(post => { if (post.layoutStyle && post.layoutStyle.startsWith('MULTI_COLUMN') && post.layoutGroupId) { if (!multiColumnGroups[post.layoutGroupId]) { multiColumnGroups[post.layoutGroupId] = { type: post.layoutStyle, posts: [] }; } multiColumnGroups[post.layoutGroupId].posts.push(post); } });
        for (const groupId in multiColumnGroups) { multiColumnGroups[groupId].posts.sort((a, b) => new Date(b.updatedAt || b.createdAt) - new Date(a.updatedAt || a.createdAt)); }
        const processedGroupIds = new Set();
        filteredPosts.forEach(post => {
            const style = post.layoutStyle || 'DEFAULT';
            if (style === 'BANNER') { if (currentDefaultBlock.length > 0) { blocks.push({ id: `default-${blocks.length}`, type: 'DEFAULT', posts: currentDefaultBlock }); currentDefaultBlock = []; } blocks.push({ id: `banner-${post.id}`, type: 'BANNER', posts: [post] }); } else if (style.startsWith('MULTI_COLUMN') && post.layoutGroupId) { if (processedGroupIds.has(post.layoutGroupId)) return; if (currentDefaultBlock.length > 0) { blocks.push({ id: `default-${blocks.length}`, type: 'DEFAULT', posts: currentDefaultBlock }); currentDefaultBlock = []; } blocks.push({ id: post.layoutGroupId, ...multiColumnGroups[post.layoutGroupId] }); processedGroupIds.add(post.layoutGroupId); } else { currentDefaultBlock.push(post); }
        });
        if (currentDefaultBlock.length > 0) { blocks.push({ id: `default-${blocks.length}`, type: 'DEFAULT', posts: currentDefaultBlock }); }
        return blocks;
    }, [filteredPosts]);
    // --- End of existing useMemo hooks ---

    // --- Keep existing Helmet logic ---
    const latestPost = useMemo(() => { if (!posts || posts.length === 0) return null; return [...posts].sort((a, b) => new Date(b.updatedAt || b.createdAt) - new Date(a.updatedAt || a.createdAt))[0]; }, [posts]);
    const pageTitle = latestPost ? `Treishvaam Finance  ${latestPost.title}` : `Treishvaam Finance | Financial News & Analysis`;
    const pageDescription = latestPost ? createSnippet(latestPost.customSnippet || latestPost.content) : "Your trusted source for financial news and analysis.";
    const imageUrl = latestPost?.thumbnails?.[0]?.imageUrl ? `${API_URL}/api/uploads/${latestPost.thumbnails[0].imageUrl}.webp` : "/logo512.webp"; // Use webp logo
    // --- End of Helmet logic ---

    const marketSliderSettings = { dots: true, infinite: true, speed: 500, slidesToShow: 1, slidesToScroll: 1, arrows: false };

    // NEW: Swipe handlers
    const swipeHandlers = useSwipeable({
        onSwipedLeft: () => {
            if (activeMobileTab === 'news') setActiveMobileTab('blog');
            else if (activeMobileTab === 'blog') setActiveMobileTab('market');
        },
        onSwipedRight: () => {
            if (activeMobileTab === 'market') setActiveMobileTab('blog');
            else if (activeMobileTab === 'blog') setActiveMobileTab('news');
        },
        preventScrollOnSwipe: true,
        trackMouse: true // Optional: allows swiping with mouse on desktop for testing
    });

    // NEW: Calculate transform based on active tab
    const getTransformStyle = () => {
        if (activeMobileTab === 'news') return 'translateX(0%)';
        if (activeMobileTab === 'blog') return 'translateX(-33.333%)'; // Move to the second view
        if (activeMobileTab === 'market') return 'translateX(-66.666%)'; // Move to the third view
        return 'translateX(0%)';
    };


    // --- Keep existing desktop layout rendering logic ---
    const renderDesktopLayout = () => {
        if (desktopLayoutBlocks.length === 0 && !loading && page === 0 && isDataReady) { return <div className="text-center p-10 col-span-full"><p>No posts found for your criteria.</p></div>; }
        return desktopLayoutBlocks.map((block, blockIndex) => {
            const isLastBlock = blockIndex === desktopLayoutBlocks.length - 1;
            const blockStyle = { marginBottom: '2rem' }; // Keep consistent spacing
            if (block.type === 'BANNER') {
                const isLastPostInBlock = block.posts.length === 1;
                return <BannerPostCard ref={isLastBlock && isLastPostInBlock ? lastPostElementRef : null} key={block.id} article={block.posts[0]} onCategoryClick={setSelectedCategory} categoriesMap={categoriesMap} />;
            }
            if (block.type.startsWith('MULTI_COLUMN')) {
                const columnCount = parseInt(block.type.split('_')[2]) || 2;
                const gridClass = `grid grid-cols-1 md:grid-cols-${columnCount} gap-px`; // Use gap-px for border effect
                return (<div style={blockStyle} key={block.id} className={gridClass}>{block.posts.map((article, postIndex) => { const isLastPost = isLastBlock && postIndex === block.posts.length - 1; return <GridPostCard ref={isLastPost ? lastPostElementRef : null} key={article.id} article={article} onCategoryClick={setSelectedCategory} categoriesMap={categoriesMap} />; })}</div>);
            }
            if (block.type === 'DEFAULT') {
                // Ensure consistent spacing for masonry layout
                return (<div style={blockStyle} key={block.id} className="sm:columns-2 md:columns-3 lg:columns-4 gap-px">{block.posts.map((article, postIndex) => { const isLastPost = isLastBlock && postIndex === block.posts.length - 1; return <PostCard ref={isLastPost ? lastPostElementRef : null} key={article.id} article={article} onCategoryClick={setSelectedCategory} categoriesMap={categoriesMap} />; })}</div>);
            }
            return null;
        });
    };
    // --- End of desktop layout rendering ---


    // --- Loading and Error states (keep as is) ---
    if (!isDataReady || (page === 0 && loading && posts.length === 0)) return <div className="flex justify-center items-center min-h-screen">Loading...</div>; // Show loading initially until categories are ready

    if (error) return (<div className="flex flex-col items-center justify-center min-h-[60vh] text-center p-6 bg-gray-50"><div className="text-red-400 mb-4"><svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1}><path strokeLinecap="round" strokeLinejoin="round" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" /></svg></div><h2 className="text-2xl font-semibold text-gray-800 mb-2">Site is Currently Under Maintenance</h2><p className="max-w-md text-gray-600">We are performing essential updates to improve your experience. We apologize for any inconvenience and appreciate your patience. Please check back again shortly.</p></div>);
    // --- End of Loading and Error states ---


    return (
        <>
            <DevelopmentNotice />
            <Helmet>
                <title>{pageTitle}</title>
                <meta name="description" content={pageDescription} />
                <link rel="canonical" href="https://treishfin.treishvaamgroup.com/" />
                <meta property="og:title" content={pageTitle} />
                <meta property="og:description" content={pageDescription} />
                <meta property="og:image" content={imageUrl} />
                <meta property="og:url" content="https://treishfin.treishvaamgroup.com/" />
                <meta property="og:type" content="website" />
                <meta name="twitter:card" content="summary_large_image" />
                <meta name="twitter:title" content={pageTitle} />
                <meta name="twitter:description" content={pageDescription} />
                <meta name="twitter:image" content={imageUrl} />
            </Helmet>

            {/* Navbar Extras Portal (keep as is) */}
            <NavbarExtrasPortal>
                <div className="w-full max-w-xs sm:w-64"> {/* Make search slightly wider on small screens */}
                    <SearchAutocomplete />
                </div>
                <div className="hidden sm:block"> {/* Hide category filter on mobile as it's inside the blog view */}
                    <CategoryFilter
                        categories={categories}
                        selectedCategory={selectedCategory}
                        setSelectedCategory={setSelectedCategory}
                        loadingCategories={loadingCategories}
                    />
                </div>
                <h1 className="text-xl font-bold text-gray-900 hidden lg:block">Finance <span className="text-sky-600">World</span></h1>
            </NavbarExtrasPortal>

            <section className="bg-gray-50 pb-16 sm:pb-0"> {/* Add padding-bottom for mobile nav space, remove for sm+ */}

                {/* --- DESKTOP LAYOUT --- */}
                <div className="hidden sm:grid grid-cols-1 lg:grid-cols-12 gap-2 max-w-screen-2xl mx-auto px-2">
                    <aside className="lg:col-span-2 order-1 py-6 sticky top-16 h-[calc(100vh-4rem)] overflow-y-auto hidden lg:block"> {/* Make sidebars sticky */}
                        <div className="space-y-4">
                            <NewsHighlights />
                            <DeeperDive />
                        </div>
                    </aside>
                    <main className="lg:col-span-8 order-2 min-h-screen py-6 bg-white px-1"> {/* Added horizontal padding */}
                        {renderDesktopLayout()}
                        {loading && page > 0 && <div className="text-center p-10 col-span-full">Loading more posts...</div>} {/* Show loading only when fetching more */}
                        {!loading && !hasMore && filteredPosts.length > 0 && <div className="text-center p-10 col-span-full text-gray-500">You've reached the end.</div>}
                    </main>
                    <aside className="lg:col-span-2 order-3 py-6 sticky top-16 h-[calc(100vh-4rem)] overflow-y-auto hidden lg:block">
                        <div className="space-y-6">
                            <IndexCharts />
                            <MarketMovers />
                        </div>
                    </aside>
                </div>

                {/* --- MOBILE LAYOUT --- */}
                <div className="sm:hidden flex flex-col h-screen"> {/* Main container for mobile */}
                    <div {...swipeHandlers} className="mobile-swipe-container flex-grow">
                        <div className="mobile-swipe-wrapper" style={{ transform: getTransformStyle() }}>

                            {/* News View */}
                            <div className="mobile-view p-4">
                                <h2 className="text-xl font-bold mb-4 pt-4">News Highlights</h2> {/* Added padding top */}
                                <NewsHighlights />
                                <DeeperDive />
                            </div>

                            {/* Blog View (Default) */}
                            <div className="mobile-view">
                                {/* Mobile Filters Toggle */}
                                <div className="px-4 py-4 sticky top-0 bg-gray-50 z-10 border-b"> {/* Make filters sticky */}
                                    <button onClick={() => setShowMobileFilters(!showMobileFilters)} className="w-full flex justify-between items-center py-2 text-md font-semibold text-gray-800">
                                        <span>{selectedCategory === 'All' ? 'Filters & Categories' : `Category: ${selectedCategory}`}</span>
                                        {showMobileFilters ? <FiX /> : <FiFilter />}
                                    </button>
                                    {showMobileFilters && (
                                        <div className="py-4">
                                            {/* Reuse BlogSidebar but maybe simplify it for mobile if needed */}
                                            <BlogSidebar
                                                categories={categories}
                                                selectedCategory={selectedCategory}
                                                setSelectedCategory={(cat) => {
                                                    setSelectedCategory(cat);
                                                    setShowMobileFilters(false); // Close filters on selection
                                                }}
                                                loadingCategories={loadingCategories}
                                            />
                                        </div>
                                    )}
                                </div>
                                {/* Mobile Posts Grid */}
                                <div className="grid grid-cols-2 gap-2 p-2">
                                    {mobileLayout.length === 0 && !loading && isDataReady && (
                                        <div className="col-span-2 text-center py-10 text-gray-500">
                                            No posts found for "{selectedCategory}"{searchTerm && ` matching "${searchTerm}"`}.
                                        </div>
                                    )}
                                    {mobileLayout.map((article, index) => {
                                        const isLastPost = index === mobileLayout.length - 1;
                                        // Pass ref only if it's the last post *and* blog tab is active
                                        const ref = (isLastPost && activeMobileTab === 'blog') ? lastPostElementRef : null;
                                        return <MobilePostCard ref={ref} key={article.id} article={article} onCategoryClick={setSelectedCategory} layout={article.layout} categoriesMap={categoriesMap} />;
                                    })}
                                </div>
                                {/* Loading/End Indicators */}
                                {loading && page > 0 && <div className="text-center p-4">Loading...</div>} {/* Show loading only when fetching more */}
                                {!loading && !hasMore && mobileLayout.length > 0 && activeMobileTab === 'blog' && <div className="text-center p-4 text-gray-500">You've reached the end.</div>}
                            </div>

                            {/* Market View */}
                            <div className="mobile-view p-4">
                                <h2 className="text-xl font-bold mb-4 pt-4">Market Data</h2> {/* Added padding top */}
                                <div className="space-y-6">
                                    <IndexCharts />
                                    <h3 className="text-lg font-semibold text-gray-900 mt-6 mb-2">Market Movers</h3>
                                    {/* Use Slider for mobile market movers */}
                                    <div className="market-slider-container pb-6 -mx-2">
                                        <style>{`.market-slider-container .slick-dots li button:before { font-size: 10px; color: #9ca3af; } .market-slider-container .slick-dots li.slick-active button:before { color: #0284c7; }`}</style>
                                        <Slider {...marketSliderSettings}>
                                            <div className="px-2"><TopMoversCard title="Most Active" fetchData={getMostActive} type="active" /></div>
                                            <div className="px-2"><TopMoversCard title="Top Gainers" fetchData={getTopGainers} type="gainer" /></div>
                                            <div className="px-2"><TopMoversCard title="Top Losers" fetchData={getTopLosers} type="loser" /></div>
                                        </Slider>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Mobile Bottom Navigation Bar */}
                    <nav className="mobile-bottom-nav">
                        <button
                            className={`mobile-nav-button ${activeMobileTab === 'news' ? 'active' : ''}`}
                            onClick={() => setActiveMobileTab('news')}
                        >
                            <FiFileText />
                            <span>News</span>
                        </button>
                        <button
                            className={`mobile-nav-button mobile-nav-logo-button ${activeMobileTab === 'blog' ? 'active' : ''}`}
                            onClick={() => setActiveMobileTab('blog')}
                        >
                            {/* Use the public path for the logo */}
                            <img src="/logo.webp" alt="Blog" />
                            {/* No text label for the logo button */}
                        </button>
                        <button
                            className={`mobile-nav-button ${activeMobileTab === 'market' ? 'active' : ''}`}
                            onClick={() => setActiveMobileTab('market')}
                        >
                            <FiActivity />
                            <span>Market</span>
                        </button>
                    </nav>
                </div>
            </section>
        </>
    );
};
export default BlogPage;